import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import Button from './Button';

const MemberFormBlock = styled.div`
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
`;

const StyledInput = styled.input`
    padding: 15px 10px;
    border-radius: 10px;
    border: 1px solid #555;
    width: 100%;
    margin: 20px 0;
`;

const MemberFoot = styled.div`
    margin-top: 20px;
    text-align: right;
    a {
        color: brown;
        text-decoration: underline;
    }
`;

/* 상황 조건에 따라 페이지 로딩 */
const textMap = {
    login: '로그인',
    join: '회원가입'
}

const MemberForm = ({type}) => {
    const text = textMap[type]
    return (
        <MemberFormBlock>
            <h3>{text}</h3>
            <form>
                <StyledInput name='userid' placeholder='아이디' type='text' />
                <StyledInput name='password' placeholder='비밀번호' type='password' />
                {
                    type === 'join' && (
                        <>
                            <StyledInput name='passwordOk' placeholder='비밀번호 확인' type='password' />
                            <label>성별: </label>
                            남 <input type='radio' name='gender' value='m' />
                            여 <input type='radio' name='gender' value='y' />
                        </>
                    )
                }
                <Button>{text}</Button>
            </form>
            <MemberFoot>
                {
                    type === 'login' ? (
                        <Link to = '/join'>회원가입</Link>
                    ) : (
                        <Link to = '/login'>로그인</Link>
                    )
                }
            </MemberFoot>
        </MemberFormBlock>
    );
};

export default MemberForm;