import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import styled from 'styled-components';
import { BsChevronLeft, BsChevronRight } from 'react-icons/bs';
import './Homesection.scss';

const SlickArticle = styled.div`
    position: relative;
    z-index: 1;
    .slick-arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
        border: none;
        padding: 10px;
        z-index: 999;
        cursor: pointer;
        width: 60px;
        height: 60px;
        &.slick-prev {
            left: 0px;
        }
        &.slick-next {
            right: 0px;
        }
    }
    .slick-dots {
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translate(-50%);
        li {
            display: inline-block;
            /* 이것의 바로 다음요소를 지정 */
            &+li {
                margin-left: 10px;
            }
            button {
                width: 15px;
                height: 15px;
                border-radius: 30px;
                overflow: hidden;
                text-indent: -99999px;
            }
        }
        li.slick-active {
            button {
                background: #ff385c;
            }
        }
    }
    .slide {
        position: relative;
        height: 40vw !important;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        .text {
            position: absolute; left: 0; top: 50%;
            transform: translateY(-50%);
            color: #fff;
            font-size: 40px;
            button {
                background: none;
                color: #fff;
                padding: 15px 25px;
                border: 1px solid #fff;
                border-radius: 50px;
                font-weight: bold;
                &:hover {
                    background: #fff;
                    color: #333;
                }
            }
        }
    }
    .slide1 {
        background-image: url(images/img3.jpg);
    }
    .slide2 {
        background-image: url(images/img5.jpg);
    }
    .slide3 {
        background-image: url(images/img7.jpg);
    }
`;

