import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const NewsItemBlock = styled.div`
    display: flex;
    margin-bottom: 20px;
    width: 50%;
    max-height: 150px;
    overflow: hidden;
    padding: 0 20px;
    .thumbnail {
        padding-right: 20px;
        width: 30%;
        img {
            width: 100%;
        }
    }
    .contents {
        width: 70%;
        h2 {
            font-size: 21px;
            margin-bottom: 5px;
            text-overflow:ellipsis;
            white-space:nowrap;
            word-wrap:normal;
            overflow:hidden;            
        }
        p {
            font-size: 14px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 3; /* 라인수 */
            -webkit-box-orient: vertical;
            word-wrap:break-word; 
            line-height: 1.5;
            height: 4.5;
        }
    }
`;

const NewsItem = ({article}) => {
    const {title, description, urlToImage} = article;
    return (
        <NewsItemBlock>
            {urlToImage && (
                <div className='thumbnail'>
                    {/* <a href={url} target='_blank' rel='noopener noreferrer'> */}
                        {/* noopener noreferrer : 외부 링크로 불러오기 */}
                    
                    <Link to={{
                        pathname: '/news/detail',
                        state: { urlToImage, title, description}
                    }}>
                        <img src={urlToImage} alt={title} />
                    </Link>
                </div>
            )}
            {description && (
                <div className='contents'>
                    <h2>
                        {title}
                    </h2>
                    <p>
                        {description}
                    </p>
                </div>
            )}
        </NewsItemBlock>
    );
};

export default NewsItem;