import React, {useEffect, useState } from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import { AiOutlineLoading3Quarters } from 'react-icons/ai'

/* 내부스타일 */
const NewsSectionBlock = styled.div`
    .row {
        padding-top: 80px;
        display: flex;
        flex-wrap:wrap;
    }
    .loadWrap {
        min-height: 800px;
        text-align:center;
        line-height: 800px;
        .loadIcon {
            font-size: 100px;
            animation: loadspin infinite 5s linear;
        }
        @keyframes loadspin {
            from {transform: rotate(0deg);}
            to {transform: rotate(3turn);}
        }
    }
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    const getNews = async () => {
        /* setLoading(true); */
        const query = category === 'all' ? '' : `&category=${category}` ;
        //${} 안에 클릭한 카테고리명이 들어가게 / & 앞에 쓰기
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=7a4dffdde62a431aa1600d90b56e3998`)
        setLoading(true);
        setArticles(response.data.articles);
    }
    useEffect (
        () => {
            getNews()
        }, [category]
    )

    if (!loading) {
        return (
            <NewsSectionBlock style={{fontSize: '72px', color:'darkslateblue'}}>
                <div className='loadWrap'>
                    <AiOutlineLoading3Quarters className='loadIcon' />
                </div>
            </NewsSectionBlock>)
    } else {
        if (!articles) {
            return <NewsSectionBlock></NewsSectionBlock>;
        } else {
            return (
                <NewsSectionBlock>
                    <div className='row'>
                        {
                            articles.map((article) => ( <NewsItem key={article.url} article={article} />))
                        }
                    </div>
                </NewsSectionBlock>
            );
        }
    }
};

export default NewsSection;